input {
  padding: 5px 12px;
  font-size: 14px;
  line-height: 20px;
  color: #24292e;
  vertical-align: middle;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: right 8px center;
  border: 1px solid #e1e4e8;
  border-radius: 6px;
  outline: none;
  box-shadow: rgba(225, 228, 232, 0.2) 0px 1px 0px 0px inset;
}
input:focus {
  border-color: #0366d6;
  outline: none;
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
}

button {
  display: inline-block;
  outline: 0;
  cursor: pointer;
  padding: 5px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  vertical-align: middle;
  border: 1px solid;
  border-radius: 6px;
  color: #ffffff;
  background-color: #111827;
  border-color: rgba(27, 31, 35, 0.1490196078);
  box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px 0px, rgba(255, 255, 255, 0.25) 0px 1px 0px 0px inset;
  transition: 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  transition-property: color, background-color, border-color;
}
button:hover {
  background-color: #111827;
  border-color: rgba(27, 31, 35, 0.1490196078);
  transition-duration: 0.1s;
}

body {
  background-color: rgba(27, 31, 35, 0.1490196078);
}

.form_container > section {
  grid-template-columns: 2fr 8fr;
}

.navbar > section:first-child > ul {
  height: 4rem;
}
.navbar > section:first-child > ul > li:not(:nth-child(1)) {
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .form_container > section {
    grid-template-columns: initial;
    grid-auto-flow: row;
  }
  .control_panel {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .control_panel button.btn {
    margin: 10px 0;
    margin-left: 0 !important;
    width: 80%;
  }
  .navbar > section:first-child > ul {
    height: auto;
    flex-direction: row;
    align-items: flex-start;
  }
  .navbar > section:first-child > ul > li:nth-child(1) {
    display: none;
  }
  .navbar > section:first-child > ul > li:not(:nth-child(1)) {
    cursor: pointer;
    margin-right: 0;
    margin-left: 0;
    padding: 1rem 2rem;
  }
}/*# sourceMappingURL=App.css.map */